@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: "Noto Sans KR" !important;
  overflow-x: hidden;
  min-width: 1920px;
  min-height: 100vh;
}

#root.dimmed::after {
  content: "";
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 10000;
}

ul,
li,
ol {
  list-style: none;
}
