.mid {
  display: flex;
  flex-direction: column; /* 세로로 나열 */
  align-items: center; /* 자식 요소들을 가운데 정렬 */
  justify-content: center; /* 가운데 배치 */
  margin: 100px; /* 위에서 약간 띄움 */
}
.login {
  display: flex; /* 자식 요소들을 플렉스 컨테이너로 만들고, 자식들을 가로 또는 세로로 정렬할 수 있게 함 */
  align-items: center; /* 플렉스 아이템(자식들)을 세로축 기준으로 중앙에 정렬 */
  justify-content: center; /* 플렉스 아이템을 가로축 기준으로 중앙에 정렬 */
  flex-direction: row; /* 플렉스 아이템들을 가로로 배치 (row는 기본값이지만 명시적으로 설정) */
  border: 1px solid rgb(241, 241, 241);
  width: 400px;
  height: 400px;
}

.logo {
  width: 80px;
  height: 40px;
  padding-left: 45px;
  margin-left: 70px;
  cursor: pointer;
  margin-bottom: 20px;
}

.loginBtn {
  display: flex; /* 자식 요소들을 플렉스 컨테이너로 만들고, 자식들을 가로 또는 세로로 정렬할 수 있게 함 */
  align-items: center; /* 플렉스 아이템(자식들)을 세로축 기준으로 중앙에 정렬 */
  justify-content: center; /* 플렉스 아이템을 가로축 기준으로 중앙에 정렬 */
  width: 350px;
  height: 50px;
  background-color: rgb(255, 0, 56);
  color: white;
  font-size: 15px;
  font-weight: bold;
  border-radius: 5px;
}

.navSignupBtn {
  font-size: 13px;
  font-weight: 500;
  margin-top: 20px; /* 로그인 영역과의 간격 */
  text-align: center; /* 텍스트 가운데 정렬 */
  cursor: pointer;
}

.idSB {
  width: 338px;
}
.pwSB {
  width: 300px;
}

.showHide {
  width: 20px;
  height: 20px;
  cursor: pointer;
  position: absolute; /* input 안쪽에 위치하도록 설정 */
  right: 10px; /* 오른쪽 끝에서 약간 떨어지게 설정 */
  top: 50%; /* 세로 정렬 */
  transform: translateY(-50%); /* 중간에 정확히 맞추기 위해 수직 변환 */
}
